<template>
  <section class="wapper">
    <!-- 左メニュー -->
    <nav class="leftMenu">
      <router-link
        :to="{
          name: 'IndexList',
          params: { course: course },
        }"
      >
        <button @click="reset">
          <span class="material-icons md-18">skip_previous</span>
        </button>
      </router-link>
      <br v-show="!isCourseIndex" />
      <button class="btn" @click="isCourseIndex = !isCourseIndex">
        <!-- &equiv; -->
        <span class="material-icons md-18">menu</span>
      </button>
      <div class="courseIndex" v-show="isCourseIndex">
        <template v-for="item in courseIndex" :key="item">
          <div class="part">{{ item.part }}</div>
          <ul>
            <li
              v-for="item in item.chapter"
              :key="item"
              :class="[{ istrue: item.href == id }]"
              v-show="item.href != '#'"
            >
              <router-link
                :to="{
                  name: 'lecture',
                  params: { course: course, id: item.href },
                }"
                :class="[{ istrue: item.href == id }]"
                @click="courseJump(item.href)"
              >
                {{ item.title }}
              </router-link>
            </li>
          </ul>
        </template>
      </div>
    </nav>

    <!-- メイン -->
    <main>
      <!-- スライドエリア -->
      <section class="slideArea">
        <img
          :src="slideImage"
          class="slideImg"
          :class="isText ? 'texton' : 'textoff'"
          alt="slide"
        />
      </section>

      <!-- メッセージエリア -->
      <section class="messageArea">
        <!-- アクター名＆操作ボタン -->
        <section class="messageMenu">
          <!-- アクター名 -->
          <section style="display: flex">
            <button
              :class="isActor ? 'isTrue' : 'isFalse'"
              @click="switchActor"
            >
              <!-- &gt;&#969;&lt; -->
              <span class="material-icons md-18">face</span>
            </button>
            <div class="actorName" :class="{ none: !isActor }">
              {{ actorName }}
            </div>
          </section>

          <!-- 操作ボタン -->
          <section class="flex">
            <button
              :class="[isText ? 'isTrue' : 'isFalse', 'textbtn']"
              @click="isText = !isText"
            >
              <!-- 字幕 -->
              <span class="material-icons md-18">textsms</span>
              <!-- &sung; -->
            </button>
            <button
              :class="isVoice ? 'isTrue' : 'isFalse'"
              @click="switchVoice"
            >
              <!-- 音声 -->
              <span class="material-icons md-18">music_note</span>
              <!-- &sung; -->
            </button>
            <button :class="isSkip ? 'isTrue' : 'isFalse'" @click="switchSkip">
              <!-- 自動 -->
              <span class="material-icons md-18">loop</span>
              <!-- &#9654; -->
            </button>
            <button @click="voiceStop">
              <!-- 停止 -->
              <span class="material-icons md-18">stop</span>
              <!-- &#9632; -->
            </button>
            <button @click="voicePlay">
              <!-- 再生 -->
              <span class="material-icons md-18">play_arrow</span>
              <!-- &orarr; -->
            </button>
            <div class="voiceSpeed">
              <span class="material-icons md-18">speed</span>
              <input
                type="range"
                min="0.5"
                max="2"
                step="0.1"
                v-model="voiceSpeed"
              />
              <div>{{ voiceSpeed }}</div>
            </div>
          </section>
        </section>

        <!-- メッセージウィンドウ -->
        <section class="messagewindow" v-show="isText" @click="next">
          <!-- アクター -->
          <section v-show="isActor">
            <img :src="actorImage" class="actor" alt="actor" />
          </section>

          <!-- メッセージ -->
          <section class="message">
            {{ messageText }}
          </section>
        </section>
      </section>
    </main>

    <!-- 右メニュー -->
    <nav class="rightMenu">
      <button @click="switchMenu">
        <span class="material-icons md-18">menu</span>
      </button>
      <ul v-show="isMenu">
        <li
          v-for="(slide, index) in scenario"
          :key="index"
          :class="{ isTrue: index == pageIndex }"
          @click="pageJump(index)"
        >
          {{ ("00" + (index + 1)).slice(-2) }}：{{ slide.title }}
        </li>
      </ul>
    </nav>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "LectureView",
  data() {
    return {
      // シナリオデータ
      course: this.$route.params.course, // パスパラメータ: コース
      id: this.$route.params.id, // パスパラメータ：ID
      courseIndex: [],
      scenario: [], // シナリオ
      // 表示制御
      isCourseIndex: false, // コースもくじ
      isText: true, // 字幕表示
      isActor: true, // キャラ表示
      isMenu: true, // もくじ表示
      isVoice: true, // 音声再生
      isSkip: true, // オートスキップ
      // 再生コントロール
      pageIndex: 0, // スライドの現在地
      messageIndex: 0, // メッセージの現在地
      messageVoice: new Audio(""), //表示メッセージ音声
      voiceSpeed: 1, // 再生速度
    };
  },
  mounted() {
    // course取得
    const courseurl = "./" + this.course + "/index.json";
    axios
      .get(courseurl)
      .then((response) => (this.courseIndex = response.data))
      .catch((error) => {
        console.log("course制作中です", error);
        this.courseIndex = [{ part: "制作中" }];
      });

    // scenario取得
    const url = `./${this.course}/${this.id}/scenario.json`;
    axios
      .get(url)
      .then((response) => (this.scenario = response.data))
      .catch((error) => {
        console.log(error);
        this.scenario = [{ part: "制作中" }];
      });
  },
  methods: {
    // scenarioを取得
    getScenario() {
      const scenariourl = "./" + this.course + "/" + this.id + "/scenario.json";
      axios
        .get(scenariourl)
        .then((response) => (this.scenario = response.data))
        .catch((error) => {
          console.log("scenario制作中です", error);
          this.scenario = [{ part: "制作中" }];
        });
    },

    // 操作切り替え
    // キャラ表示
    switchActor() {
      this.isActor = !this.isActor;
    },
    // 切替：音声
    switchVoice() {
      this.isVoice = !this.isVoice;
      if (!this.isVoice) {
        this.isSkip = false;
      }
    },
    // 切替：オートスキップ
    switchSkip() {
      this.isSkip = !this.isSkip;
      if (this.isSkip) {
        this.isVoice = true;
      }
    },
    // 切替：目次表示
    switchMenu() {
      this.isMenu = !this.isMenu;
    },
    // 移動：もくじジャンプ
    pageJump(index) {
      this.pageIndex = index;
      this.messageIndex = 0;
      this.messageVoice.pause();
      if (this.isVoice) {
        this.voicePlay();
      }
    },
    // 移動：シナリオジャンプ
    courseJump(href) {
      this.messageVoice.pause();
      this.id = href;
      this.getScenario();
      this.pageIndex = 0;
      this.messageIndex = 0;
    },

    // 再生コントロール
    // 進む
    next() {
      this.messageVoice.pause();
      this.messageIndex++;
      // 各スライドの最終メッセージ⇒次のスライド
      if (this.messageText == "LAST") {
        if (this.scenario.length == this.pageIndex + 1) {
          this.messageIndex--;
          return;
        } else {
          this.pageIndex++;
          this.messageIndex = 0;
        }
      }

      // 音声ありの時のみ再生
      if (this.isVoice) {
        this.voicePlay();
      }
    },

    // ボイスセット⇒再生
    voicePlay() {
      // 再生中なら一旦停止
      this.messageVoice.pause();
      // ボイスの有無を判断してセット
      let voice =
        this.scenario[this.pageIndex].message[this.messageIndex].voice;
      // ボイスが無い時は進む
      if (!voice) {
        this.messageIndex++;
        this.voicePlay();
      } else {
        this.messageVoice = new Audio(
          "./" + this.course + "/" + this.id + "/voice/" + voice
        );
        this.messageVoice.playbackRate = this.voiceSpeed;
        this.messageVoice.play();
        // ボイス終了⇒オートスキップ
        this.messageVoice.addEventListener("ended", () => {
          if (this.isSkip) {
            this.next();
          }
        });
      }
    },

    // ボイス中断
    voiceStop() {
      this.messageVoice.pause();
    },

    // 前のページに戻る際にリセット
    reset() {
      this.messageVoice.pause();
      this.pageIndex = 0;
      this.messageIndex = 0;
    },
  },
  computed: {
    // スライドイメージ
    slideImage() {
      try {
        if (!this.scenario[this.pageIndex].message[this.messageIndex].slide) {
          return "./common/background/cloud2.png";
        } else {
          return `./${this.course}/${this.id}/img/${
            this.scenario[this.pageIndex].message[this.messageIndex].slide
          }`;
        }
      } catch {
        return { mes: "制作中です" };
      }
    },

    // アクター
    actorName() {
      try {
        if (!this.scenario[this.pageIndex].message[this.messageIndex].actor) {
          return "No Name";
        } else {
          return this.scenario[this.pageIndex].message[this.messageIndex].actor;
        }
      } catch {
        return "No Name";
      }
    },
    actorImage() {
      try {
        if (
          !this.scenario[this.pageIndex].message[this.messageIndex].actorImg
        ) {
          return "./common/actor/Tsumugi-01.png";
        } else {
          return `./common/actor/${
            this.scenario[this.pageIndex].message[this.messageIndex].actorImg
          }`;
        }
      } catch {
        return "./common/actor/Tsumugi-01.png";
      }
    },

    // メッセージテキスト
    messageText() {
      try {
        return this.scenario[this.pageIndex].message[this.messageIndex].text;
      } catch {
        return "LAST";
      }
    },
  },
};
</script>

